// components/Nexus.tsx
'use client';
import React, { useEffect, useRef, useState } from 'react';

export default function Nexus({ result }: { result?: any }){
  const canvasRef = useRef<HTMLCanvasElement|null>(null);
  const [tick, setTick] = useState(0);
  useEffect(()=>{
    let raf = 0;
    function draw(){
      const canvas = canvasRef.current; if(!canvas) return;
      const ctx = canvas.getContext('2d')!;
      const w = canvas.width, h = canvas.height;
      ctx.clearRect(0,0,w,h);
      const g = ctx.createLinearGradient(0,0,0,h); g.addColorStop(0,'rgba(4,12,20,0.6)'); g.addColorStop(1,'rgba(2,6,10,0.6)');
      ctx.fillStyle = g; ctx.fillRect(0,0,w,h);
      const idx = result?.index ?? 50;
      const chaos = result? (idx>65?0.6: idx>40?1.2:2.8) : 1.5;
      const hue = idx>65?150: idx>40?45:0;
      const cols = Math.round(18 + (1/Math.max(1, chaos))*20);
      for(let i=0;i<cols;i++){
        const baseX = (i+0.5)/cols * w;
        let x = baseX + 40*Math.sin((tick + i*20)/80);
        let y = 20;
        ctx.beginPath(); ctx.moveTo(x,y);
        const segments = 6;
        for(let s=0;s<segments;s++){
          const cx = x + (Math.random()-0.5)*80*chaos + 20*Math.sin((tick + i*10 + s*5)/40);
          const cy = y + (s+1) * (h/(segments+1)) + 10*Math.cos((tick + i*7 + s*11)/40);
          const nx = baseX + (s+1)*(w/(segments+2) - w/(cols*segments)) + Math.sin((tick + s*i)/30)*60*chaos;
          ctx.quadraticCurveTo(cx,cy,nx,cy+10);
          x = nx; y = cy+10;
        }
        ctx.lineWidth = 0.8 + 1.6*(1 - idx/100);
        ctx.strokeStyle = `hsla(${hue},80%,${45 + 20*Math.sin((tick+i)/30)}%,${0.35 + (0.7 - chaos/4)})`;
        ctx.stroke();
      }
      const cx = w*0.75, cy = h*0.5;
      const pulse = 20 + (result? (result.index/100)*60 : 30) * Math.abs(Math.sin(tick/40));
      const g2 = ctx.createRadialGradient(cx,cy,pulse*0.1,cx,cy,pulse*3);
      g2.addColorStop(0,`hsla(${hue},90%,60%,${0.22})`); g2.addColorStop(1,'rgba(10,12,18,0)');
      ctx.fillStyle = g2; ctx.beginPath(); ctx.arc(cx,cy,pulse*3,0,Math.PI*2); ctx.fill();
      setTick(t => t+1);
      raf = requestAnimationFrame(draw);
    }
    raf = requestAnimationFrame(draw);
    return ()=>cancelAnimationFrame(raf);
  },[result, tick]);
  return (
    <div style={{display:'flex',gap:12,alignItems:'flex-start'}}>
      <canvas id="nexusCanvas" ref={canvasRef} width={800} height={340} style={{borderRadius:10, display:'block', background:'linear-gradient(180deg, rgba(2,8,14,0.6), rgba(3,12,20,0.6))'}} />
      <div style={{width:280}}>
        <div style={{background:'rgba(255,255,255,0.02)', padding:10, borderRadius:10}}>
          <div style={{display:'flex',alignItems:'center',gap:10}}>
            <div className="pill" id="nexusMoodTag">{ result ? (result.index>65?'Harmonic': result.index>40?'Tense':'Stressed') : '—' }</div>
            <div style={{flex:1}}>
              <div className="muted-block">Symbiosis index</div>
              <div id="nexusIndex" style={{fontWeight:700,fontSize:22}}>{ result ? result.index : '—' }</div>
            </div>
          </div>
          <div style={{marginTop:8}}>
            <div className="muted-block">Timeline</div>
            <input id="timeSlider" type="range" min={0} max={100} defaultValue={100} />
            <div style={{display:'flex', gap:8, marginTop:8}}>
              <button className="btn" id="recompute">Recompute Visuals</button>
              <button className="btn ghost" id="exportPNG" onClick={()=>{
                const canvas = canvasRef.current; if(!canvas) return;
                const a = document.createElement('a'); a.href = canvas.toDataURL('image/png'); a.download = 'gaia_nexus.png'; a.click();
              }}>Export PNG</button>
            </div>
          </div>
        </div>
        <div style={{marginTop:12, background:'rgba(255,255,255,0.02)', padding:10, borderRadius:10}}>
          <div className="muted-block">Interpretation</div>
          <div id="nexusText" style={{marginTop:8}}>{ result ? `ΔT ${result.tempDrift}°C · Biodiversity ${result.biodiversity}/100 · Sea +${result.seaLevel}m. Filaments visualize ecosystem stress.` : 'Run a simulation to populate Nexus with a living structure built from the prediction.' }</div>
        </div>
      </div>
    </div>
  );
}
